import React from 'react';
import { Link } from 'react-router-dom';
import './Home.css'

const Home = () => {
    return (
        <div className="rapper">
            <h1 className="title">Slave's Work</h1>

            <section className="container">
                <ul className="home">
                    <li>
                        <Link id="host" to={{
                            pathname: "/host"
                        }} className="HostButton">Host</Link>
                    </li>
                    <li>
                        <Link to={{
                            pathname: "/client"
                        }} className="clientButton">Client</Link>
                    </li>
                </ul>
            </section>
        </div>
    )
}

export default Home;