import React, { useState } from 'react';


const useConnect = () => {
    const [devices, setDevices] = useState([]);

    const addDevices = () => {
        setDevices([...devices, { key: 1, name: 'DeskTop', status: '100ms' }]);
    }

    return { devices, addDevices };
}

const Connect = () => {
    const { devices, addDevices } = useConnect()
    // console.log(dev)
    return (
        <div className="half_container devices">
            <h2 className="sub_title">Devices</h2>
            {devices.length > 0 &&
                <ul>
                    {devices.map(device => (
                        <li key={device.key}>
                            <p className="device_name">{device.name}</p>
                            <p className="device_status">{device.status}</p>
                        </li>
                    ))}
                </ul>
            }
            {devices.length === 0 &&
                <div className="center">
                    <p>현재 연결된 디바이스가 없습니다.</p>
                </div>
            }
            <button onClick={addDevices}>개발자 버튼</button>
        </div>
    );
}

export default Connect;