import React, { useState } from 'react';

const useHashToken = (init) => {
    const [hashToken, setHashToken] = useState("");

    const randomToken = () => {
        setHashToken("slaveswork")
    }
    return [hashToken, randomToken];
}

const Token = () => {
    const [hashToken, randomToken] = useHashToken("");
    
    return (
        <div className="half_container">
            <h2 className="sub_title">Connect</h2>
            <div className="token_div">
                <p>Token : </p>
                <p className="token">{hashToken}</p>
                <button id="electron_start" onClick={randomToken}>Start</button>
            </div>
        </div>
    );
}

export default Token